
.box-book-info figure {
  width: 175px;
  height: 350px;
  float: left;
  position: relative;
  padding: 10px 10px 0 10px;
  cursor: pointer;
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: .2s ease-in-out;
}
.box-book-info:hover figure {
  -webkit-transform: scale(1.02);
  transform: scale(1.02);
  box-shadow: 0.6px 0.8px 13.3px 2.7px rgba(34, 30, 31, 0.08);
  border: solid 1px #4196c2;
}
.book-info-title {
  width: 150px;
  height: 20px;
  float: left;
}
.book-info-start-rate {
  width: 100px;
  height: 20px;
  float: left;
}
.book-info-author {
  width: 150px;
  height: 20px;
  float: left;
}
.book-info-margin {
  margin-top: 10px;
}
.book-info-box-title {
  height: 20px;
  width: 100%;
}



