
.target-user-info {
  margin-top: 50px;
  height: 60px;
  padding-top: 5px;
}
.book-request-image {
  width: 200px;
  height: 290px;
}
.target-book-info {
  margin-top: 30px;
  height: 290px;
}
.book-request-detail {
  position: relative;
  padding-left: 35px;
  width: 600px;
  height: 290px;
}
.request-history {
  margin-top: 5px;
}
.update-request-group-btn {
  position: absolute;
  bottom: 0;
}
.btn-update-request-wrapper {
  margin-right: 20px;
}
.btn-update-request {
  width: 200px;
}
.request-empty {
  height: 60px;
  padding: 4px 15px;
  border: 1px solid white;
}
.each-request {
  height: 60px;
  padding: 3px 15px;
  transition-duration: 0.4s;
  border: 1px solid white;
}
.each-request-not-bottom {
  border-bottom: 1px solid #eaeaea;
}
.each-request:hover {
  border: 1px solid #4196c2;
  box-shadow: 0.6px 0.8px 10px 2.7px rgba(34, 30, 31, 0.1);
}
.display-right .half-row-top {
  text-align: right;
}
.display-right .half-row-bottom {
  text-align: right;
}
.detail-link {
  color: #117bb2;
}
.request-nav-form {
  margin: 15px 5px;
  background-color: #f8f8f8;
  min-width: 270px;
  height: calc(100vh - 90px);
  min-height: 440px;
}
.request-tab {
  margin: 25px 20px 0 20px;
}
.request-type {
  margin-top: 14px;
  color: #7e7e7e;
  cursor: pointer;
  transition-duration: 0.4s;
}
.request-type:hover {
  color: #a2ddfc;
}
.request-type.active {
  color: #a2ddfc;
}
.progress-stick {
  height: 5px;
  width: 120px;
  margin-left: 6px;
  margin-right: 6px;
}
.success-stick {
  background-color: #4196c2;
}
.success-progress-des {
  color: #4196c2;
}
.fail-stick {
  background-color: red;
}
.fail-progress-des {
  color: red;
}
.hold-on-stick {
  background-color: #e1e1e1;
}
.hold-on-progress-des {
  color: #e1e1e1;
}
.success-part {
  height: 40px;
  width: 40px;
  border-radius: 20px;
  background-color: #4196c2;
}
.fail-part {
  height: 40px;
  width: 40px;
  border-radius: 20px;
  background-color: red;
}
.hold-on-inner-part {
  padding: 3px;
  color: #7e7e7e;
  height: 30px;
  width: 30px;
  border-radius: 15px;
  background-color: #e1e1e1;
}
.progress-wraper {
  position: relative;
}
.progress-des {
  position: absolute;
  width: 120px;
  top: 50px;
  right: -40px;
}
.hold-on-part {
  position: relative;
  padding: 3px;
  height: 40px;
  width: 40px;
  border-radius: 20px;
  border: 2px solid #e1e1e1;
}



