.tab-heading-background {
    min-width: 1185px;
    height: 40px;
    background: #a2ddfc;
}

.fixed-tab-header {
    position: fixed;
    z-index: 900;
    top: 60px;
    left: 0;
    right: 0;
}

.tab-header-tab{
    padding-top: 4px;
    height: 50px;
    text-align:center;
}

//tab modify
.nav-tabs-header {
    white-space:nowrap;
    border-bottom: 2px solid #DDD;
}

.nav-tabs-header>li {
    cursor: pointer;
    display: inline-block;
}

.nav-tabs-header>li.active>a,
.nav-tabs-header>li.active>a:focus,
.nav-tabs-header>li.active>a:hover {
    border-width: 0;
}

.nav-tabs-header>li>a {
    border: none;
    color: #666;
}

.nav-tabs-header>li.active>a,
.nav-tabs-header>li>a:hover {
    border: none;
    color: #4196c2 !important;
    background: transparent;
}

.nav-tabs-header>li>a::after {
    content: "";
    background: #4196c2;
    height: 2px;
    position: absolute;
    width: 101%;
    left: 0px;
    right: 0px;
    bottom: 5px;
    transition: all 250ms ease 0s;
    transform: scale(0);
}

.nav-tabs-header>li.active>a::after,
.nav-tabs-header>li:hover>a::after {
    transform: scale(1);
}



