
.book-shelf-na-tab {
  width: 12.5%;
}
.book-shelf-tab {
  width: 25%;
  text-align: center;
  font-size: 14px;
}
.user-info-content {
  padding: 30px 40px;
}
.user-info-each {
  height: 40px;
  line-height: 40px;
}
.email-pass-row {
  height: 46px;
  line-height: 46px;
}
.button-min-width {
  position: absolute;
  width: 30px;
  height: 30px;
  padding: 2px 0 0 1px;
}
.button-social-min-width {
  position: absolute;
  width: 30px;
  height: 30px;
  padding: 5px 0 0 0px;
}
.gmap-area {
  height: 200px;
  margin-top: 15px;
}
.split-content {
  margin: 35px 0;
  border-top: 1px solid #c8c8c8;
}
.input-group-addon {
  background-color: white;
  color: gray;
}
.upload-image-container {
  margin: 20px;
  height: 360px;
  text-align: center;
  width: 360px;
  border: 1px solid #c8c8c8;
}
#uploader {
  cursor: pointer;
  color: #505050;
}
#upload-photo {
  opacity: 0;
  position: absolute;
  z-index: -1;
}
.reset-image-button {
  position: absolute;
  bottom: 15px;
  left: 20px;
}



