
.header-book-search-history {
  position: relative;
  cursor: pointer;
  width: 100%;
  height: 51px;
  padding-top: 8px;
  padding-bottom: 8px;
}
.header-book-search-history-left {
  float: left;
  border-radius: 50%;
  background-color: #d5d5d5;
  height: 35px;
  width: 35px;
  text-align: center;
  position: relative;
}
.header-book-search-history-left img {
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.header-book-search-history-right {
  float: right;
  text-align: left;
  position: relative;
  width: 390px;
  padding-left: 20px;
  padding-top: 8px;
}



