
.notification-title {
  width: 1160px !important;
  position: fixed;
  z-index: 100;
  top: 60px;
  background-color: white;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #c8c8c8;
}
.notification-item {
  width: 100%;
}
.notify-date {
  position: relative;
  color: #4196c2;
  height: 100%;
  padding-left: 15px;
}
.notify-content {
  position: relative;
  padding: 5px 10px;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid #eaeaea;
  border-left: 1px solid #eaeaea;
  border-right: 1px solid #eaeaea;
}
.need-border-top {
  border-top: 1px solid #eaeaea;
}
.notification-user-border {
  width: 44px;
  height: 44px;
  border-radius: 22px;
  border: 2px solid #8ec3df;
}
.line-height-20 {
  height: 20px;
  line-height: 20px;
}
.line-height-30 {
  height: 25px;
  line-height: 30px;
}
.book-title-in-notice {
  color: #111111;
}
.notification-bottom-right {
  position: absolute;
  bottom: 6px;
  right: 10px;
}
.fa-bookmark {
  position: absolute;
  bottom: 8px;
  right: 10px;
  color: #2e2e2e;
}
.fa-bookmark:hover {
  color: #000000;
}
.fa-bookmark-o {
  position: absolute;
  bottom: 8px;
  right: 10px;
  color: #2e2e2e;
}
.fa-bookmark-o:hover {
  color: #000000;
}
.notifi-arrow-down {
  position: absolute;
  bottom: 25px;
  right: 6px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #303030;
}
.mark-as-read-popup {
  position: absolute;
  bottom: 33px;
  right: -53px;
  width: 140px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: white;
  background-color: #303030;
  border-radius: 5px;
}



