.user-info-form{
    margin: 15px 5px;
    background-color: #f8f8f8;
    min-width: 270px;
    min-height: calc(100vh - 90px);
    padding-top: 33px;
}

.user-profile-image{
    width: 152px;
    height: 152px;
    border-radius: 76px;
    border: 2px solid #4196c2;
}

.user-name{
    margin-top: 32px;
    text-align: center;
    color: #4196c2;
    width: 160px;
}

.address{
    margin-top: 20px;
    text-align: center;
    width: 200px;
}

.user-navigation{
    margin-top:28px;
    width: 100%;
}

.navigation-button{
    width: 100%;
    padding-left: 50px;
    height: 50px;
    line-height: 50px;
    color: #7e7e7e;
    border-top: 1px solid white;
    cursor: pointer;
}

.navigation-button-bottom{
    border-bottom: 1px solid white;
}

.navigation-button:hover{
    color: black;
    background-color: #cfeeff;
}

.navigation-button.active{
    color: black;
    background-color: #cfeeff;
}

.gmap-nearby-user{
    margin: 15px;
    padding-top: 15px;
    min-width: 240px;
    height: 255px;
}


