$text-color-default: #2b2b2b;
$text-color-highlight: #4196c2;
$text-color-highlight-number: #7cc576;
$text-color-btn: white;
$text-color-author: #7e7e7e;
$text-color-category: #929292;
$text-color-category-select: black;
$text-color-address: #7c7c7c;
$text-color-checked: #ffff00;

/*font Montserrat-Medium*/

.small-text-form-m {
    font-family: "Montserrat";
    font-size: 12px;
    font-weight: 500;
}

.medium-text-form-m {
    font-family: "Montserrat";
    font-size: 14px;
    font-weight: 500;
}

.biger-text-form-m {
    font-family: "Montserrat";
    font-size: 16px;
    font-weight: 500;
}

.super-biger-text-form-m {
    font-family: "Montserrat";
    font-size: 18px;
    font-weight: 500;
}

.super-super-biger-text-form-m {
    font-family: "Montserrat";
    font-size: 24px;
    font-weight: 500;
}


/*font Montserrat-Regular*/

.small-text-form-r {
    font-family: "Montserrat";
    font-size: 12px;
    font-weight: 400;
}

.medium-button-text {
    font-family: "Montserrat";
    font-size: 13px;
    font-weight: 400;
}

.medium-text-form-r {
    font-family: "Montserrat";
    font-size: 14px;
    font-weight: 400;
}

.medium-large-text-form-r {
    font-family: "Montserrat";
    font-size: 15px;
    font-weight: 400;
}

.biger-text-form-r {
    font-family: "Montserrat";
    font-size: 16px;
    font-weight: 400;
}

.super-biger-text-form-r {
    font-family: "Montserrat";
    font-size: 18px;
    font-weight: 400;
}

.super-super-biger-text-form-r {
    font-family: "Montserrat";
    font-size: 24px;
    font-weight: 400;
}

.text-color-default {
    color: $text-color-default;
}

.text-color-highlight {
    color: $text-color-highlight;
}

.text-color-btn {
    color: $text-color-btn;
}

.text-color-highlight-number {
    color: $text-color-highlight-number;
}

.text-color-author {
    color: $text-color-author;
}

.text-color-category {
    color: $text-color-category;
}

.text-color-category-select {
    color: $text-color-category-select;
}

.text-color-address {
    color: $text-color-address;
}

.text-limit {
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.text-style-bold {
    font-weight: bold;
}

.title-name-color {
    color: #202020;
}

.txt-show-more {
    text-align: center;
    vertical-align: middle;
    display: table-cell;
    color: $text-color-btn;
}


