.message-list-form {
    margin: 15px 5px;
    background-color: #ffffff;
    min-width: 270px;
    min-height: 500px;
}

.message-target-list {
    overflow-y: scroll;
    height: calc(100vh - 140px);
}

.message-container {
    overflow-y: scroll;
    height: calc(100vh - 190px);
    padding-bottom: 15px;
}

.write-message-box {
    border-top: 1px solid #c8c8c8;
    height: 50px;
    line-height: 47px;
}

.input-message-form {
    height: 40px;
    margin-left: 15px;
    width: 800px;
    border: none;
}

.input-message-form:focus{
    outline: none;
}

.send-message-button {
    width: 35px;
    height: 35px;
    padding-left: 9px;
}

.each-conversation {
    height: 60px;
    padding: 3px 15px;
    border-bottom: 1px solid white;
    cursor: pointer;
}

.each-conversation.read {
    background-color: #fafafa;
}

.each-conversation.not-read {
    background-color: #f1faff;
}

.each-conversation:hover{
    background-color: #f4f4f4;
}

.each-conversation.active{
    background-color: #eeeeee;
}

.message-content-container{
    display: inline-block;
    margin-top: 8px;
    padding: 5px 10px;
    line-height: 25px;
    max-width: 350px;
    border-radius: 25px;
    word-wrap: break-word;
}

.message-content-container.target-user{
    border: 1px solid #4196c2;
}

.message-content-container.your-own{
    background-color: #ebebeb;
}

.split-by-date {
    height: 60px;
    line-height: 60px;
    text-align: center;
}


