.home-box-top-user {
    width: 100%;
    height: 450px;
    padding-top: 35px;
}

.box-avatar {
    height: 70px;
    width: 110px;
    position: relative;
    text-align: left;
}

.number-user {
    margin-top: 10px;
    color: #f50909;
    float: left;
    margin-right: 20px;
}

.row-user {
    width: 100%;
    height: 70px;
    margin: auto;
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
}

.row-user:hover {
    background-color: aliceblue !important;
}



