.login-form-wrapper {
    width: 768px;
    display: block;
    padding-top: calc(50vh - 299px);
    padding-bottom: calc(50vh - 299px);
}

.login-form {
    height: 420px;
}

.social-login-form {
    background-color: #4196c2;
    height: 100%;
}
.email-login-form {
    background-color: #ffffff;
    height: 100%;
}

.free-row-top {
    height:30px;
    background-color: $box-color-category;
}

.free-row-bottom {
    height:88px;
    line-height: 88px;
    text-align: center;
    background-color: $box-color-category;
}

.email-login-form{
    //padding-top: 6px;
    padding-left: 22px;
    padding-right: 22px;
}

.login-signup-tab{
    width:50%;
    padding-top: 4px;
    height: 50px;
    text-align:center;
    font-size: 16px;
}

.btn-email{
    width: 100%;
    height: 40px;
}

.social-login-box{
    margin-left: 13px;
    margin-right: 13px;
}

.gat-icon-container{
    margin-top: 51px;
}

.gat-icon{
    width: 50%;
    height: auto;
}

.info-container{
    margin-top: 24px;
    padding-top: 2px;
    padding-bottom: 2px;
    color: #ffffff;
}

.social-button-group{
    margin-top: 43px;
}

.btn-social {
    width: 100%;
    height: 40px;
}


