.book-detail-list-user-comment-form-input {
  resize: none;
  width: 750px;
}
.book-detail-list-user-comment-form-action {
  width: 760px;
  height: 50px;
  padding-top: 5px;
  float: left;
  position: relative;
}
.book-detail-comment-rate-box {
  color: #2b2b2b;
  margin-top: 20px;
  position: relative;
  float: left;
  width: 240px;
  height: 100%;
}
.book-detail-list-user-comment-txt-rate {
  float: left;
  position: relative;
  margin-right: 20px;
}
.book-detail-list-user-comment-rate-start {
  float: left;
  position: relative;
}
.book-detail-list-user-comment-create {
  width: 145px;
  height: 40px;
  border-radius: 20px;
  background-color: #4196c2;
  color: white;
  position: relative;
  margin-top: 10px;
  float: right;
}
.book-detail-list-user-comment-create:hover {
  background-color: #a2ddfc;
}
.book-detail-list-user-comment-cancel {
  width: 145px;
  height: 40px;
  border-radius: 20px;
  margin-top: 10px;
  margin-left: 20px;
  float: right;
}
.book-detail-list-user-comment-cancel:hover {
  background-color: #a2ddfc;
}
.book-detail-content-user-comment-form {
  width: 100%;
  min-height: 125px;
  position: relative;
  display: inline-block;
  padding-top: 15px;
  margin-bottom: 20px;
}



