
.image-container {
    position: relative;
}
.on-hover-effect {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.image-middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%)
}
.image-container:hover .on-hover-effect{
    opacity: 0.3;
}
.image-container:hover .image-middle {
    opacity: 1;
}




