
.bookstop-info-form {
  margin-top: 1px;
  height: 300px;
}
.bookstop-search-form {
  height: 60px;
  border-bottom: 1px solid #e1e1e1;
}
.search-item {
  margin-top: 12px;
  margin-left: 20px;
}
.bookstop-booklist {
  min-height: 640px;
  margin: 10px 50px 15px 50px;
  padding-top: 15px;
}
.bookstop-image-top {
  height: 280px;
  padding: 25px;
  border-bottom: 1px solid #e1e1e1;
}
.text-6-row {
  margin-top: 8px;
  height: 114px;
  line-height: 19px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}
.circle-25 {
  width: 25px;
  height: 25px;
  border-radius: 13px;
  text-align: center;
  padding-top: 3px;
}
.circle-25.red {
  background-color: #ff0000;
}
.circle-25.blue {
  background-color: #00aeef;
}
.inline-limit {
  width: 100%;
  line-height: 25px;
}
.bookstop-image-bottom {
  min-height: 420px;
  margin: 10px 30px 15px 30px;
  padding-top: 15px;
}
.bookstop-background-image {
  height: 230px;
}
.bookstop-avatar {
  position: absolute;
  bottom: 0;
  left: 45px;
  width: 225px;
  height: 225px;
  border: 5px solid #8ec3df;
}
.bookstop-info {
  position: absolute;
  bottom: 0;
  left: 300px;
  max-width: 500px;
}
.bookstop-contact {
  position: absolute;
  bottom: 0;
  right: 0;
}
.bookstop-nav-form {
  background-color: #f8f8f8;
}
.bookstop-nav-button {
  width: 100%;
  padding-left: 50px;
  height: 50px;
  line-height: 50px;
  color: #7e7e7e;
  border-top: 1px solid white;
  cursor: pointer;
}
.bookstop-nav-button.active {
  color: black;
  background-color: #cfeeff;
}
.most-book-title {
  height: 50px;
  line-height: 50px;
  background-color: #f8f8f8;
  border-bottom: 1px solid #e1e1e1;
}
.paging-button-group {
  padding-top: 7px;
}
.paging-button {
  width: 26px;
  height: 26px;
  cursor: pointer;
  position: relative;
  border: 1px solid #d1d1d1;
}
.paging-button .fa {
  color: #d1d1d1;
}
.paging-button.active {
  background-color: #cfeeff;
}
.paging-button.active:hover {
  background-color: #c2d8e5;
}
.paging-button.active .fa {
  color: #55a1c9;
}
.paging-button.active:hover .fa {
  color: #4397c3;
}
.fa-angle-left-custom {
  position: absolute;
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  top: 4px;
  left: 9px;
}
.fa-angle-right-custom {
  position: absolute;
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  top: 4px;
  left: 11px;
}
.mostbook-layout {
  height: 160px;
  padding-top: 17px;
}
.mostbook-split-border {
  height: 1px;
  margin: 0 15px 0 15px;
  background-color: #e1e1e1;
}
.mostbook-image-form {
  width: 90px;
  height: 126px !important;
}
.mostbook-image {
  width: 100%;
  height: 100%;
}



