.forgot-form-wrapper {
    width: 520px;
    display: block;
    padding-top: calc(50vh - 299px);
    padding-bottom: calc(50vh - 299px);
}

.forgot-form {
    padding-left: 60px;
    padding-right: 60px;
    height: 280px;
    background-color: #ffffff;
}

.forgot-free-row-top {
    height:50px;
    background-color: $box-color-category;
}

.forgot-free-row-bottom {
    height:100px;
    line-height: 100px;
    text-align: center;
    background-color: $box-color-category;
}

.forgot-title {
    
    width: 400px;
    text-align: center;
    height: 50px;
    line-height: 50px;
    border-bottom: 2px solid #4196c2;
    color: #4196c2;
}

.btn-forgot {
    width: 100%;
}


