
.list-bookstop-container {
  width: 100%;
  position: relative;
  height: auto;
  margin-bottom: 35px !important;
}
.list-bookstop-content-right {
  width: 875px;
  min-height: 600px;
  float: right;
  position: relative;
}
.list-bookstop-content-left {
  width: 295px;
  position: relative;
  float: left;
  padding-right: 20px;
}
.per-bookstop-info {
  padding: 5px;
  height: 120px;
}
.per-bookstop-info:hover {
  box-shadow: 0.6px 0.8px 13.3px 2.7px rgba(34, 30, 31, 0.08);
}
.list-bookstop-index {
  height: 20px;
  width: 100%;
}
.list-bookstop-index-map-marker {
  widows: 11px;
  height: 15px;
  color: #ec5a5a;
  margin-right: 10px;
}
.list-bookstop-index-book {
  widows: 11px;
  height: 15px;
  margin-right: 10px;
}



