@import '../../../../scss/common_styles.scss';
.share-book-content {
    width: 100%;
    height: 510px;
    margin-top: 15px;
    position: relative;
}

.share-book-box-left {
    width: 570px;
    height: 506px;
    background-color: $box-color-header;
    position: relative;
    float: left;
}

.share-book-box-right {
    width: 305px;
    height: 510px;
    padding-left: 25px;
    position: relative;
    float: right;
    padding-right: 8px;
}

.share-book-row-user {
    width: 100%;
    height: 70px;
    margin: auto;
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
}

.share-book-row-user:hover {
    background-color: aliceblue !important;
}

.share-book-box-avatar {
    height: 70px;
    width: 80px;
    position: relative;
    text-align: left;
}

.share-book-bok-username {
    width: 180px;
    height: 20px;
}


